import React from 'react';
import {Card, Image, Icon} from 'semantic-ui-react';

const MetaID = ({metaUser}) => {
  const { name, joined, about } = metaUser;

  return (
      <Card>
        <Image src='/assets/images/avatar/large/matthew.png' />
        <Card.Content>
          <Card.Header>
            {name}
          </Card.Header>
          <Card.Meta>
        <span className='date'>
          {joined}
        </span>
          </Card.Meta>
          <Card.Description>
            {about}
          </Card.Description>
        </Card.Content>
        <Card.Content extra>
          <a>
            <Icon name='star' />
            4 stars
          </a>
        </Card.Content>
      </Card>
  )
};

export {MetaID};


